        </el-form-item>
    </el-form>
</template>
<script>
export default {
    data() {
      return {
        ruleForm: {
          name: '',
          input: ''
        },
      }
    },
    methods: {
      submit() {
            if (this.ruleForm.input == "wmm&yhh999" && this.ruleForm.name == "90days") {
                this.$message({
                    message: '提示:wynteryhh.github.io/show',
                    type: 'success'
                });
            }
      },
    }
}
</script>

