<template>
  <el-autocomplete
    clearable
    style="width:720px"
    class="inline-input"
    v-model="state"
    :fetch-suggestions="querySearch"
    placeholder="三个月"
    :trigger-on-focus="false"
    @select="handleSelect"
    >
    </el-autocomplete>
</template>

<script>
export default {
    data() {
      return {
        restaurants: [],
        state: '',
      };
    },
    methods: {
      querySearch(queryString, cb) {
        var restaurants = this.restaurants;
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        // 调用 callback 返回建议列表的数据
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      loadAll() {
        return [
         
          { "value": "小狐狸~密码:wmm&yhh999", "address": "" },
          { "value": "我是大笨蛋~账号:90days", "address": "" },
          { "value": "叶涵涵与王孟孟在一起90天啦~好喜欢你", "address": "" },
          { "value": "王孟孟是哥哥,哥哥的提示:尝试输入'我'试一下", "address": "" },
          { "value": "三个月啦好喜欢你", "address": "" }
        ];
      },
      handleSelect(item) {
        console.log(item);
      }
    },
    mounted() {
      this.restaurants = this.loadAll();
    }
  }

</script>

